import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Chanson} from './chanson/chanson';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {YoutubbService} from './youtubb.service';
import {YoutubbComponent} from './youtubb/youtubb.component';
import {DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';

@Injectable({
  providedIn: 'root'
})
export class ChansonService {
  public uurl: string ;
 public chansonTab: Chanson[] = [];

  constructor(public http: HttpClient, protected sanitizer: DomSanitizer ) { }

  parseChanson(rep: any, artist: string ): Chanson[] {
    this.chansonTab = [];
    for ( let chans  of rep.album.tracks.track) {
      this.chansonTab.push(new Chanson(chans.name, artist, ''));
     // console.log(chans.name);
    }
    return this.chansonTab;
  }

  loadChansons( nomArtist: string , nomAlb: string): Observable<any> {
    return this.http.get<any>('https://ws.audioscrobbler.com/2.0/?method=album.getinfo&api_key=9a8a3facebbccaf363bb9fd68fa37abf&artist=' + nomArtist + '&album=' + nomAlb + '&format=json')
      .pipe(
        map(rep => this.parseChanson(rep, nomArtist) )
      );

  }

  parsUrl (rep: any): string {
    this.uurl = '';
    this.uurl = 'https://www.youtube.com/embed/';
   // console.log('LOAD videoId');
   // console.log(rep.items[0].id.videoId);
    this.uurl = this.uurl + rep.items[0].id.videoId;
    return rep.items[0].id.videoId;
  }
}

