import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Artist} from './artist';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Album} from './album';
import {isBoolean} from 'util';

@Injectable({
  providedIn: 'root'
})
export class ArtistService  {

 arts: Artist[] = [];

  constructor(public http: HttpClient) {
   const t: Artist[] = JSON.parse( localStorage.getItem('tout'));
     if ( t != null) {
       this.arts = t;
     }
  }

  loadArtist( artistName): Observable<Artist> {
    return this.http.get<any>(
      'https://ws.audioscrobbler.com/2.0/?method=artist.getinfo&api_key=9a8a3facebbccaf363bb9fd68fa37abf&format=json&artist=' + artistName)
      .pipe(
        map(repp => this.parsArts(repp))
      );
  }
  parsArts (re: any): Artist {
    let artistCour: Artist;
    artistCour = new Artist(re.artist.name, re.artist.image[4]['#text']);

    if (artistCour.nom != null && artistCour.image !== '') {
      if (!this.exist(artistCour.nom)) {
        this.arts.push(artistCour);

        localStorage.setItem('tout', JSON.stringify(this.arts));
      }
      return artistCour;
    }
  }

  public exist (name: String): boolean {
      for (let ar of this.arts  ) {
      if (ar.nom === name) {
        return true;
      }
    }
    return false;
  }



}
