import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
 export class YoutubbService {
public uurl: string ;
  constructor(public http: HttpClient) { }

  LoadYtubUrl (nomArtist: string, nomChanson: string): Observable<any> {

    return this.http.get<any>('https://www.googleapis.com/youtube/v3/search?part=id&maxResults=1&key=AIzaSyDMVfcBkTVqhDw2NoU2qj_2GPeWBhR__48&q=' + nomChanson + '%20' + nomArtist)
      .pipe(map(rep => this.parsUrl(rep) ));
  }
  parsUrl (rep: any): string {
    this.uurl = 'https://www.youtube.com/embed/';
   /* console.log('LOAD videoIdn');
    console.log(rep.items[0].id.videoId);*/
    this.uurl = this.uurl + rep.items[0].id.videoId;
    return this.uurl;
  }
}
