import { Injectable } from '@angular/core';
import {Artist} from './artist';
import {Spectacle} from './spectacle';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Venue} from './venue';

@Injectable({
  providedIn: 'root'
})
export class SpectacleService {

  spect: Spectacle[] = [];

  constructor( public http: HttpClient) { }

  LoadSpectalses ( artistName): Observable <Spectacle[]> {
    return this.http.get<any>('https://rest.bandsintown.com/artists/' + artistName + '/events?app_id=MERCI')
      .pipe(
        map(reps => this.parsSpectacle(reps))
      );
  }

  parsSpectacle (rep: any): Spectacle[] {
   // const ven = new Venue( rep.venue.contry, rep.venue.city, rep.venue.latitude, rep.venue.name, rep.venue.region, rep.venue.longitude);
this.spect = [];
  let sp: Spectacle;
  for ( const r of rep) {
    sp = new Spectacle( Number( r.venue.latitude) , r.lineup[0], Number( r.venue.longitude), r.datetime, r.venue.name );
    this.spect.push(sp);
  }

    return this.spect;
  }
}
