import { Component, OnInit } from '@angular/core';
import {DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';
import {ActivatedRoute} from '@angular/router';
import {ChansonService} from '../chanson.service';
import {YoutubbService} from '../youtubb.service';
@Component({
  selector: 'app-youtubb',
  templateUrl: './youtubb.component.html',
  styleUrls: ['./youtubb.component.css']
})
export class YoutubbComponent  implements OnInit {
  idChans: string;
  url = 'https://www.youtube.com/embed/' + this.idChans;

  constructor( protected sanitizer: DomSanitizer, public route: ActivatedRoute, public ser: YoutubbService) { }

  ngOnInit() {
    const artistS = this.route.snapshot.paramMap.get('artist');
    const titre = this.route.snapshot.paramMap.get('chanson');
    this.ser.LoadYtubUrl(artistS, titre).subscribe(r => this.idChans = r);
  }
  getNomArtist(): string {
    return this.route.snapshot.paramMap.get('artist');
  }
  getSafeUrl(): SafeResourceUrl { return this.sanitizer.bypassSecurityTrustResourceUrl(this.ser.uurl); }
}
