import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Album} from './album';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AlbumService {
// tabAlbum: Album[] = [];
  constructor(public http: HttpClient) { }

  loadAlbum(art: string): Observable<Album[]> {
  /*  console.log("LOAD ALBUM");
    console.log(art);*/


    return this.http.get<any>(
      'https://ws.audioscrobbler.com/2.0/?method=artist.gettopalbums&artist=' + art + '&api_key=9a8a3facebbccaf363bb9fd68fa37abf&format=json&format=json' )
      .pipe(
        map(repon => this.parsAlbum(repon)));
  }

  parsAlbum( repon: any): Album [] {
    let tabAlbum: Album[] = [];
  for (let alb of repon.topalbums.album) {

    tabAlbum.push(new Album(alb.name, alb.image[3]['#text'], alb.artist.name));
   /* console.log("PARAM");
    console.log(alb.image);*/
  }

    return tabAlbum;
}

}
