import { Component, OnInit } from '@angular/core';
import {Album} from '../album';
import {Artist} from '../artist';
import {ChansonService} from '../chanson.service';
import {Chanson} from './chanson';
import {ActivatedRoute} from '@angular/router';
import {YoutubbService} from '../youtubb.service';
import {DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';


@Component({
  selector: 'app-chanson',
  templateUrl: './chanson.component.html',
  styleUrls: ['./chanson.component.css']
})
export class ChansonComponent implements OnInit {

tabChanson: Chanson[] = [];

  constructor(public ser: ChansonService, public route: ActivatedRoute, public serY: YoutubbService, protected sanitizer: DomSanitizer) { }

  ngOnInit() {
   const artistS = this.route.snapshot.paramMap.get('artist');
    const albumS = this.route.snapshot.paramMap.get('album');
   // console.log("PARAM");
   // console.log(albumS);

    this.ser.loadChansons(artistS, albumS).subscribe(r => {
      this.tabChanson = r;
      for (let c of this.ser.chansonTab) {
        console.log('gettttUrlr');

        this.serY.LoadYtubUrl(c.artist, c.titr).subscribe(u => { c.url = u; });
      }
    });
  }
  getNomArtist(): string {
    return this.route.snapshot.paramMap.get('artist');
  }
  getSafeUrl( pUrl: string ): SafeResourceUrl { return this.sanitizer.bypassSecurityTrustResourceUrl(pUrl); }

}
