import { Component, OnInit } from '@angular/core';
import {ArtistService} from '../artist.service';
import {Artist} from '../artist';
import {Album} from '../album';
import {AlbumService} from '../album.service';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-artist',
  templateUrl: './artist.component.html',
  styleUrls: ['./artist.component.css']
})
export class ArtistComponent implements OnInit {
public name: string = null;


  public artisttTab: Artist[] = [];


 public artMapTab: Map<string, Artist> = new Map<string, Artist>();

  /////////////////////////////////4.1) dlia perevoda   v constryktor dobavil i metod dobavil
    constructor(public ser: ArtistService, public translate: TranslateService) {
      translate.setDefaultLang('fr');
    }
  changerLangue(langue: string) {
    this.translate.use(langue);
  }
  //////////////////////////////////////////////////////////////////////////////
    ngOnInit() {
      if (this.name != null ) {
          this.ser.loadArtist(this.name).subscribe(r => {

            this.artisttTab.push(r);
            this.artMapTab.set(r.nom, r);
            let a: Artist = this.artMapTab.get(r.nom);
          });

       this.name = '';
        }
      }

      toArray(): Artist[] {
        return Array.from(this.artMapTab.values());
      }


  }





