import { Component, OnInit } from '@angular/core';
import {SpectacleService} from '../spectacle.service';
import {Spectacle} from '../spectacle';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-spectacle',
  templateUrl: './spectacle.component.html',
  styleUrls: ['./spectacle.component.css']
})
export class SpectacleComponent  implements OnInit {
  //lat = 51.678418;
  //lng = 7.809007;

 public name: string = null;
  public tabSpec: Spectacle[] = [];

  constructor( public ser: SpectacleService, public route: ActivatedRoute) { }

  ngOnInit() {
    this.name = this.route.snapshot.paramMap.get('artist');/////////////////////////VAZHNO!!!!!!!!!!!!!!!!!!!!!!!!!!
    if ( this.name != null) {
      this.ser.LoadSpectalses(this.name).subscribe(s => {this.tabSpec. push(s[0]); } );
    }
  }

}
